import React, { Component } from 'react';

class SubBbs extends Component {

    render(){
        function activeMethod(event) {
            const current = event.currentTarget;
            const chkActive = current.classList.value.indexOf("active");
            console.log(current);
            closeMothodAll();
        
            if (chkActive === -1) {
                current.classList.add("active");
            }
        }

        function closeMothodAll() {
            let bx = document.getElementsByClassName("bbs-list");
        
            for (let i = 0; i < bx[0].children.length; i++) {
                bx[0].children[i].classList.remove("active");
        
            // console.log(bx[0].children.length);
            }
        }
        const bbs_items = [
            {
                id: 1,
                subject : '2018 10 23  ',
                text : '   <br/> : 2912',
                date : '2019-01-15'
            },
            {
                id: 2,
                subject : '2018 0907  ',
                text : '',
                date : '2018-09-07'
            },
            {
                id: 3,
                subject : '2017 118 (5)',
                text : '<br><br><br><br>',
                date : '2017-11-20'
            },
            {
                id: 4,
                subject : '   ',
                text : ' : 2014 1003 ~ 04<br> : <br> : , MTB() <br><br>  CH   .',
                date : '2016-10-31'
            },
            {
                id: 5,
                subject : 'CH   ',
                text : ' .<br>   .<br>  .',
                date : '2016-03-09'
            },
        ];

        const bbs_item = bbs_items.map((el)=>
            <div className="bbs-list__item" key={el.id} onClick={activeMethod}>
                <h3 className="bbs-list__item__title">
                    {el.subject}
                </h3>
                <div className="bbs-list__item__content">
                    <p dangerouslySetInnerHTML={{__html : el.text}}>
                    </p>
                </div>
                <p className="bbs-list__item__date">
                    {el.date}
                </p>
                <span className="bbs-list__item__btn">
                    <span className="blind"></span>
                </span>
            </div>
        )

        return(
            
                <div className="bbs-list">
                    {bbs_item}
                </div>
            
        )
    }
}

export default SubBbs;
