import React, { Component } from 'react';
import SubBbs from '../../components/sub_bbs/sub_bbs';
import '../../css/board.scss';

class news extends Component{
    render(){
        return(
            <main className="container">
                <article>

                    <header className="subtop subtop--bg1">
                        <div className="inner">
                            <div className="subtop__content">
                                <h2 className="subtop__title">CH NEWS</h2>
                                <p className="subtop__text">
                                            CHSOFT.
                                </p>
                            </div>
                        </div>
                    </header>

                    <div className="contents">
                        <div className="inner">
                            <div className="board">
                                <p className="board__text">
                                    20  10  
                                </p>
                                <h3 className="board__title">
                                    CH NEWS
                                </h3>
                                <SubBbs/>
                            </div>
                        </div>
                    </div>

                </article>
            </main>
        )
    }
}

export default news;