import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../../css/technology.scss';

class Homepage extends Component {
    render(){
        return(
            <main className="container">
                <article>

                    <header className="subtop subtop--bg1">
                        <div className="inner">
                            <div className="subtop__content">
                                <h2 className="subtop__title">TECHNOLOGY</h2>
                                <p className="subtop__text">
                                            CHSOFT.
                                </p>
                            </div>
                        </div>
                    </header>

                    <div className="contents">
                        <div className="inner">
                            <div className="technology">
                                <p className="technology__text">
                                       .
                                </p>
                                <h3 className="technology__title">
                                    HOMEPAGE
                                </h3>

                                <div className="tab">
                                    <ul className="tab__list">
                                        <li className="tab__item">
                                            <Link to="/homepage" className="tab__link active">
                                                <span></span>
                                            </Link>
                                        </li>
                                        <li className="tab__item">
                                            <Link to="/erp" className="tab__link"> 
                                                <span>ERP</span>
                                            </Link>
                                        </li>
                                        <li className="tab__item">
                                            <Link to="/bookerp" className="tab__link">
                                                <span>ERP</span>
                                            </Link>
                                        </li>
                                    </ul>
                                </div>

                                <div className="technology__info">
                                    <div className="technology__info__image">
                                        <p>
                                            <img src={require("../../img/tech/tech_img_01.png")} alt=""/>
                                        </p>
                                    </div>
                                    <div className="technology__info__content">
                                        <p className="technology__info__text1">
                                            ,    
                                        </p>
                                        <h4 className="technology__info__title">
                                                !
                                        </h4>
                                        <p className="technology__info__text2">
                                               , ,                  .
                                            ,   
                                        </p>
                                        <div className="technology__info__btn-box">
                                            <Link to="/contactus" className="js-btn" rel="noopener noreferrer">
                                                <span>, </span>
                                            </Link>
                                            <a href="/portfolio" className="js-btn" rel="noopener noreferrer">
                                                <span> </span>
                                            </a>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>

                </article>
            </main>
        )
    }
}

export default Homepage;